.overview {
  position: relative; }

.overviewContent {
  margin-bottom: 10px; }

.overviewContainer {
  background-color: #4d4d4f;
  z-index: 100;
  position: relative; }

.overview p, h3 {
  color: white; }

.overviewInner {
  overflow: hidden;
  text-align: center; }

.overview-title {
  line-height: 1.2; }

.feature {
  display: inline-block;
  /* width: 20%; */
  vertical-align: middle; }

.blurb {
  display: inline-block;
  /* width:20%; */
  vertical-align: text-top; }

.blurb h3 {
  margin-bottom: 5px; }

.blurb p {
  width: 80%;
  margin: 0 auto; }

.feature img {
  height: 50%;
  width: 50%; }

@media (max-width: 459px) {
  .feature {
    font-size: 10px;
    margin: 0 7px 0 7px;
    width: 27%; }
  .blurb h3 {
    font-size: 9px; }
  .blurb p {
    width: 100%;
    font-size: 8px; }
  .blurb {
    width: 27%;
    margin: 0 7px 0 7px; }
  .overview-title {
    font-size: 15px; } }

@media (min-width: 460px) and (max-width: 767px) {
  .blurb h3 {
    font-size: 9px; }
  .blurb p {
    width: 100%;
    font-size: 8px; }
  .blurb {
    width: 27%;
    margin: 0 7px 0 7px; }
  .feature {
    font-size: 10px;
    margin: 0 7px 0 7px;
    width: 27%; } }

@media (min-width: 768px) {
  .blurb {
    width: 27%;
    margin: 0 15px 0 15px; }
  .feature {
    font-size: 10px;
    margin: 0 15px 0 15px;
    width: 27%; }
  .blurb p {
    width: 80%;
    font-size: 11px; }
  .blurb h3 {
    font-size: 15px; } }

@media (min-width: 992px) {
  .overview-title {
    font-size: 25px; }
  .blurb p {
    font-size: 16px; } }

@media (min-width: 1200px) {
  .overview-title {
    font-size: 30px; }
  .feature {
    margin: 0 auto;
    width: 30%; }
  .blurb {
    margin: 0 auto;
    width: 30%; }
  .blurb p {
    width: 60%; }
  .overviewInner {
    margin-left: 30px;
    margin-right: 30px; } }
